import React, { Component } from "react";
import { TimelineLite, Elastic } from "gsap/all";
import portrait from '../../portrait.png';
import squirrel1 from '../../squirrel-1.png';
import squirrel2 from '../../squirrel-2.png';
import squirrel3 from '../../squirrel-3.png';
import squirrel4 from '../../squirrel-4.png';
import squirrel5 from '../../squirrel-5.png';
import squirrel6 from '../../squirrel-6.png';
import github from '../../GitHub-Mark.png';
import './About.css'

// icons will be animated using a stagger method
const iconsArray = [
	{ src: squirrel1, width: "93", height: "73" },
	{ src: squirrel2, width: "93", height: "73" },
	{src: squirrel3, width: "93", height: "73" },
	{src: squirrel4, width: "93", height: "73" },
	{src: squirrel5, width: "84", height: "63" },
	{src: squirrel6, width: "117", height: "78" }



];

class About extends Component {

	constructor(props){
		super(props);

		this.logoTl = new TimelineLite({ paused:true });

		this.content = null;
		this.head = null;
		this.subhead = null;
		this.feature = null;
		this.contact = null;
		this.nutcracker = null;
		this.github = null;
		this.icons = [];
	}

	// add instances to the timeline
	componentDidMount(){
		this.logoTl
			.set(this.content, { autoAlpha: 1 })// show content div
			.from(this.head, 0.5, { left: 100, autoAlpha: 0 })
			.from(this.subhead, 0.5, { left: -100, autoAlpha: 0 }, "-=0.25") // added -0.25 seconds prior to end this.of timeline
			.from(this.feature, 0.5, { scale: .5, autoAlpha: 0 }, "+=0.5") // added 0.5 seconds after end of timeline
			.from(this.nutcracker, 0.5, { scale: .5, autoAlpha: 0 }, "+=0.5") // added 0.5 seconds after end of timeline
			.staggerFrom(this.icons, 0.2, { scale: 0, autoAlpha: 0 }, 0.1) //animate all icons with 0.1 second stagger
			.from(this.contact, 0.5, { scale: .5, autoAlpha: 0, ease: Elastic.easeOut, y: 200  }, "+=0.5")
			.from(this.github, 0.5, { scale: .5, autoAlpha: 0 }, "+=0.5")

	}

	render(){
		return <div className="container mt-5 mb-5">
			<div className="row">
				<div className="col-sm-12">

					<h3 className="text-center">About me</h3>
					<p className="lead">I have a degree in economics and have been working in various professions such as band manager, cameraman, in post production, illustration or motion design. But in the end of the year 2017...</p>

					<div className="row">
            <div className="my-3 btn-group mx-auto mt-5">
              <button
                className="btn btn-primary blackBtn"
                onClick={() => this.logoTl.play()}
              >Play</button>
              <button
                className="btn btn-primary"
                onClick={() => this.logoTl.pause()}
              >Pause</button>
              <button
                className="btn btn-primary"
                onClick={() => this.logoTl.reverse()}
              >Reverse</button>
              <button
                className="btn btn-primary"
                onClick={() => this.logoTl.restart()}
              >Restart</button>
            </div>
          </div>
					{/* DEMO WRAPPER */}
					<div className="demoWrapper">

						<div className="content" ref={ div => this.content = div }>
            <h4 className="text-center" ref={ h4 => this.head = h4 }>...I discovered a passion for Web Development...</h4>
							<p className="lead" ref={ p => this.subhead = p }>and attended a 1 year intensive training at the DCI Institute in Berlin with main focus on Javascript, React, Nodejs and express. After graduation in 2019 I am looking for a position as a junior web developer.</p>

							<div className="mb-5">
								<img
									src={portrait}
									width="210"
									height="294"
									className="d-block mx-auto"
                  ref={ img => this.feature = img }
                  alt="an illustration of me"
								/>

							</div>
							<p className="lead" ref={ p => this.nutcracker = p }>Kind regards JÃ¼rgen Schuler 2019</p>


							<div className="nav imgContainer">
								{ iconsArray.map( (icon, index) => {
									const { src, width, height } = icon;
									return <img
										className="m-1"
                    key={`icon-${index}`}
                    alt="mushroom-drawings"
										src={src} width={width} height={height}
										ref={ img => this.icons[index] = img }
									/>;
                })}

							</div>
							<p className="mt-5" ref={ p => this.contact = p }>behighspy@yahoo.de</p>
							<a ref={ a => this.github = a } href="https://github.com/Bierwurst" target="_blank" rel="noopener noreferrer"><img src={github} alt=""/></a>
						</div>

					</div>
				</div>
			</div>
		</div>;
	}

}

export default About;
