import React, {Fragment, Component} from 'react';
import video from '../../welcomeScreen.mp4';

import './welcome.css';

class Welcome extends Component {
	constructor (props) {
		super(props);

		this.state = {
				videoURL: video
		}
}

render () {
		return (
			<Fragment>
							<div style={{width: "100vw", height: "100vh", position: "fixed", top: "0px", left: "0px", zIndex: "-1"}}>
									<video loop autoPlay muted style={{width: "100%", height: "100%", objectFit: "cover"}}>
									<source src={this.state.videoURL} type="video/mp4" />
									<source src={this.state.videoURL} type="video/ogg" />
									Your browser does not support the video tag.
								</video>
							</div>

			</Fragment>



		)
}
  }

export default Welcome
