import React from 'react';
import {Carousel} from 'react-bootstrap';

import './Trains.css';
import Titel from './assets/01_Titel_Signika.png';
import Page2 from './assets/02-sama-nozug.png';
import Page3 from './assets/03-sama-zug.png';
import Page4 from './assets/04-aufzug.png';
import Page5 from './assets/05-alex.png';
import Page6 from './assets/06-regional.png';
import Page7 from './assets/07-blauer.png';
import Page8 from './assets/08-gueter.png';
import Page9 from './assets/09-sbahn.png';
import Page10 from './assets/10-ICE.png';
import Page11 from './assets/11-notrain.png';
import Page12 from './assets/12-toytrain.png';
import Page13 from './assets/13inBed.png';


const images = [Titel, Page2, Page3, Page4, Page5, Page6, Page7, Page8, Page9, Page10, Page11, Page12, Page13];

class Trains extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.handleSelect = this.handleSelect.bind(this);
    this.content = null;

    this.state = {
      index: 0,
      direction: null,
      interval: false

    };
  }

  handleSelect(selectedIndex, e) {
    this.setState({
      index: selectedIndex,
      direction: e.direction
    });
  }

  render() {

 
    const { index, direction} = this.state;


    return (

      <div ref={ div => this.content = div } className="col-md-8 m-mx-auto m-my-5 col-sm-10 mx-sm-auto my-sm-5 col-10 mx-auto my-5">
      {console.log(this.props)}
        <Carousel
          className="content"
          activeIndex={index}
          direction={direction}
          onSelect={this.handleSelect}
          interval={false}
        >
          {images.map((n, i) =>
            <Carousel.Item>
              <img
                key={i}
                className="d-block w-100 imageClass"
                src={`${n}`}
                alt={`slide number ${i}`}
              />
            </Carousel.Item>
          )}

        </Carousel>
      </div>


    );

  }

}


export default Trains
